.buy-units-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
    margin: 50px auto;
    background-color: #f9f9f9;
    border-radius: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 600px;
  }
  
  .buy-units-container h2 {
    font-size: 28px;
    color: #007bff;
    margin-bottom: 10px;
  }
  
  .buy-units-container p {
    font-size: 16px;
    color: #666;
    margin-bottom: 20px;
  }
  
  .buy-units-form {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 15px;
  }
  
  .buy-units-form label {
    font-size: 16px;
    color: #333;
    display: flex;
    flex-direction: column;
  }
  
  .buy-units-form input,
  .buy-units-form select {
    padding: 12px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .buy-units-form button {
    padding: 12px;
    background-color: #28a745;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .buy-units-form button:hover {
    background-color: #218838;
  }
  